
*{margin:0;padding:0
}
.content{margin:auto;max-width:1200px
}
.btnbtn{display:inline-block;line-height:1;white-space:nowrap;cursor:pointer;background:#fff;border:1px solid #c0ccda;color:#1f2d3d;text-align:center;box-sizing:border-box;outline:none;margin:20px 10px 0px 0px;padding:9px 15px;font-size:14px;border-radius:4px;color:#fff;background-color:#50bfff;border-color:#50bfff;transition:all .2s ease;text-decoration:none;user-select:none
}
.show-info h2{line-height:50px
}
.test{height:500px
}
@keyframes slide{
0%{background-position:0 0
}
100%{background-position:-100% 0
}
}
.file-upload-container *{box-sizing:border-box
}
.file-upload-container .file-upload-icon{display:inline-block;border:1px solid #000000;position:relative;z-index:1;cursor:pointer;height:100%;width:100%
}
.file-upload-container .file-upload-icon:before,.file-upload-container .file-upload-icon:after{content:'';display:block;position:absolute;z-index:1;top:50%;left:50%;transform:translate(-50%, -50%)
}
.file-upload-container .file-upload-icon:before{width:40%;height:1px;background-color:#000000
}
.file-upload-container .file-upload-icon:after{height:40%;width:1px;background-color:#000000
}
.file-upload-container input{display:block;height:100%;width:100%;opacity:0;position:absolute;z-index:3;cursor:pointer
}
.crop-container{position:fixed;width:80%;left:50%;top:50%;transform:translate(-50%, -50%);padding:20px;background-color:#ffffff;box-shadow:0 0 0 1000px rgba(0,0,0,0.7);text-align:center;border-radius:4px;z-index:9999
}



