
.img-item{
  flex-direction: row;
  display: flex;
  margin-top: 10px;
}
.img-item .detail-img-box{
  width: 100px;
  margin-left: 10px;
}
.img-item .detail-img-box img{
  width: 100%;
}
.placeHold{
  width: 100%;
  height:30px;
}
.item-box-title {
  width: 40%;
}
.item-box-content{
  width: 40%;
  text-align: right;
}
.item-box-content div{
  text-align: right;
  margin-top: 5px;
}
.item-box{
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding: 10px;
  border-bottom-color: #ebebeb;
  border-bottom-style: solid;
  border-bottom-width: 1px;
}
.detail-box{
  width: 80%;
  background-color: white;
  padding: 30px;
  background-color: #f7f7f7;
  /*box-shadow: 5px 5px 5px #888888;*/
  margin-top: 10px;
  margin-left: 10%;
}



